.wrapper {
  width: 500px;
  height: 500px;
  margin: auto;
  position: relative;
}
.jump {
  width: 250px;
  height: 390px;
  background: url("../../../../images/jump.png") no-repeat;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: bottom, left 0.3s;
}
.pos_0 {
  background-position-x: 0;
  background-position-y: 0;
}
.pos_1 {
  background-position-x: -330px;
  background-position-y: 0;
}
.pos_2 {
  background-position-x: -620px;
  background-position-y: 0;
}
.pos_3 { 
  background-position-x: -950px;
  background-position-y: 0;
  bottom: 100px;
  left: 100px;
}
.pos_4 {
  background-position-x: -1245px;
  background-position-y: 0;
  left: 100px;
}
.pos_5 {
  background-position-x: -1555px;
  background-position-y: 0;
  left: 100px;
}

