.input,
.input_dark_theme {
  background-color: #e9ecef;
  color: #212529;
  border: none;
  height: 50px;
  width: 190px;
  text-align: left;
  padding: 10px;
  margin: 5px;
}
.input:disabled {
  background-color: #fff;
}
.input_dark_theme:disabled {
  background-color: #495057;
  border: none;
}
.input_dark_theme {
  background-color: #495057;
  color: #dee2e6;
  border: 1px solid #dee2e6;
}