.background {
  display: block;
  border-radius: 50px;
  padding: 100px;
  max-width: 500px;
  margin: auto;
  cursor: pointer;
}
.background__active {
  background-color: #8B00FF;
}
.picture {
  display: block;
  margin: auto;
  max-width: 100%;
  border: 5px solid transparent;
}
.picture__active {
  border: 5px solid #000;
}