.tab_list,
.tab_list_dark_theme {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  border-bottom: 1px solid #dee2e6;
  justify-content: center;
}
.tab_list_dark_theme {
  border-bottom: 1px solid #495057;
  background-color: #dee2e6;
}